<template>
	<div class="confirm-order">
		<div class="good-detail">
			<div class="good-img">
				<img src="../assets/clothes-bg.png">
			</div>
			<div class="goods-label">
				<p class="good-title"></p>
				<p class="good-size">L</p>
				<p class="good-price">89.00</p>
			</div>
		</div>

		<div class="nearby-machine">
			<p class="title"></p>
			<div class="map" id="container" style="height:12.5rem"></div>
			<div class="location">
				<img src="../assets/ico-location.png">
				<span>111</span>
			</div>
		</div>

		<ul class="deals-list">
			<li class="deals-items">
				<div class="deals-title"></div>
				<div class="deals-value"></div>
			</li>
		</ul>

		<div class="bottom-bar">
			<div class="total">89.00</div>
			<div class="submit-btn"></div>
		</div>
	</div>
</template>

<script>
import AMap from 'AMap'
export default {
	name: 'ConfirmOrder',
	data () {
		return {
			msg:""
		}
	},
	created (){
		document.title="";
		window.scrollTo(0, 0);
	},
	mounted () {
		this.init()
	},
    methods: {
      init: function () {
        var map, geolocation;
	    //
	    map = new AMap.Map('container', {
	        resizeEnable: true,
	        zoom: 11,
	    });
	    map.plugin('AMap.Geolocation', function() {
	        geolocation = new AMap.Geolocation({
	            enableHighAccuracy: true,//:true
	            timeout: 10000,          //10
	            buttonOffset: new AMap.Pixel(10, 20),//Pixel(10, 20)
	            zoomToAccuracy: true,      //false
	            buttonPosition:'RB'
	        });
	        map.addControl(geolocation);
	        geolocation.getCurrentPosition();
	        AMap.event.addListener(geolocation, 'complete', onComplete);//
	        AMap.event.addListener(geolocation, 'error', onError);      //
	    });
	    function onComplete(data) {
	        // var str=[''];
	        // str.push('' + data.position.getLng());
	        // str.push('' + data.position.getLat());
	        // if(data.accuracy){
	        //     str.push('' + data.accuracy + ' ');
	        // }//IP
	        // str.push('' + (data.isConverted ? '' : ''));
	        // document.getElementById('tip').innerHTML = str.join('<br>');
	        alert('')
	    }
	    //
	    function onError(data) {
	        // document.getElementById('tip').innerHTML = '';
	        alert('')
	    }
      }
    }
}
</script>

<style scoped>
	.good-detail{
		display: flex;
		display: -webkit-flex;
		padding: 1rem;
	}
	.good-img{
		width: 5rem;
		height: 5rem;
		border: 1px solid #e5e5e5;
		padding: 0.2rem;
		margin-right: 1rem;
	}
	.good-title{
		padding: 0.2rem 0;
	}
	.good-size{
		color:#999;
		font-size: 0.75rem;
	}
	.good-price{
		padding: 1rem 0;
	}
	.nearby-machine .title{
		padding: 1rem;
	}
	.nearby-machine .location{
		font-size: 0.875rem;
		color: #666;
		padding: 0.5rem;
		display: flex;
		display: -webkit-flex;
		align-items: center;
		-webkit-align-items: center;
		justify-content: center;
		-webkit-justify-content: center;
	}
	.nearby-machine .location img{
		width:0.7rem;
		margin-right: 0.5rem;
	}
	.deals-list{
		padding: 1.5rem 1rem;
	}
	.deals-items{
		display: flex;
		display: -webkit-flex;
		align-items: center;
		-webkit-align-items: center;
		justify-content: space-between;
		-webkit-justify-content: space-between;
		padding-right: 1rem;
		background: url(../assets/ico-right.png) right no-repeat;
		background-size: auto 0.75rem;
	}
	.deals-value{
		color: #999;
	}
	.bottom-bar{
		width: 100%;
		display: flex;
		display: -webkit-flex;
		align-items: center;
		-webkit-align-items: center;
		justify-content: flex-end;
		-webkit-justify-content: flex-end;
		position: fixed;
		left: 0;
		bottom: 0;
	}
	.bottom-bar .total{
		padding: 0.8rem 1.5rem;
	}
	.bottom-bar .submit-btn{
		background-color: #000;
		color: #fff;
		padding: 0.8rem 1.5rem;
	}
</style>



